import { EOrientation } from "@gdk/core";

import { LockView as BaseLockView } from "../../fdj/views/lockView";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class LockView extends BaseLockView {
	/**
	 * Constructor
	 * @param vi the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		// To avoid default process
	}

	/**
	 * Sets the orientation of the game and then what message to show
	 */
	public set orientation(orientation: EOrientation) {
		if (orientation !== EOrientation.Both) {
			throw new Error(`SVS lockview does only handle "Both" orientation`);
		}
	}
}
