import { LoadedView, Docking, AssetStore, isLeftPointerTap, Resize } from "@gdk/core-pixi";
import { ESettingsEvent, SettingsEvent, tracker } from "@gdk/core";
import { Rectangle, Sprite, Graphics } from "pixi.js";
import type { Container } from "pixi.js";
import type { Button } from "@gdk/core-pixi";

import { EVisualIdentityAction, EVisualIdentityEvent } from "../../../events";
import { SettingsComponent } from "../components/settingsComponent";
import { TweenUtils } from "../../../../game";
import type { IAdaptativeInGameView } from "../../../identity";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

export class InGameView extends LoadedView implements IAdaptativeInGameView {
	/**
	 * Button used to display rules
	 */
	public settingsButton: Button = null;
	/**
	 * Defines the settings component used to draw the settings window, buttons etc.
	 */
	public settingsComponent: SettingsComponent;
	/**
	 * Indicates if the sound is enabled
	 */
	private _soundEnabled: boolean = true;

	/**
	 * A custom cursor
	 */
	private _cursor: Sprite = null;

	/**
	 * Overlay that captures clics outside of the settingsComponent to close the inGameView
	 */
	public interactiveOverlay: Graphics = null;

	/**
	 * Constructor.
	 * @param vi the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();

		this.settingsComponent = new SettingsComponent(vi);
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		this.interactiveOverlay = new Graphics().beginFill(0x000000, 0).drawRect(0, 0, 1, 1).endFill();
		this.interactiveOverlay.resize = new Resize(Resize.cover);
		this.interactiveOverlay.interactive = true;
		this.interactiveOverlay.visible = false;

		this.addChildAt(this.interactiveOverlay, 0);

		this.interactiveOverlay.on("pointertap", () => {
			this.vi.emitter.emit(EVisualIdentityEvent.HideSettings);
			this.settingsComponent.visible = false;
			this.interactiveOverlay.visible = false;
		});

		const hideInteractiveOverlay = () => (this.interactiveOverlay.visible = false);
		this.vi.emitter.on(EVisualIdentityEvent.ShowRules, hideInteractiveOverlay);
		this.vi.emitter.on(EVisualIdentityEvent.ShowWinningTable, hideInteractiveOverlay);

		this.settingsButton.on("pointertap", () => {
			this.settingsButton.enabled = false;
			this.interactiveOverlay.visible = true;
			this.vi.emitter.once(EVisualIdentityEvent.HideSettings, () => {
				tracker.track(new SettingsEvent(ESettingsEvent.Hide));
				this.settingsButton.enabled = true;
			});

			tracker.track(new SettingsEvent(ESettingsEvent.Show));

			this.vi.emitter.emit(EVisualIdentityEvent.ShowSettings);
			this.settingsComponent.show();
			if (this._cursor) {
				this.vi.game.stage.addChildAt(this._cursor, this.vi.game.stage.children.indexOf(this) + 1);
			}
		});

		this.settingsComponent.visible = false;

		this.vi.emitter.on(EVisualIdentityAction.SettingsEnabled, (enabled: boolean) => {
			this.settingsButton.enabled = enabled;
		});
	}

	/**
	 * On the view will appear
	 */
	public onWillAppear(): void {
		this._setCursor();

		if (!this.vi.parameters.hideInGameView) {
			this.visible = true;
		}
	}

	/**
	 * Returns if the sound is enabled
	 */
	public get soundEnabled(): boolean {
		return this._soundEnabled;
	}

	/**
	 * Sets if the sound is enabled
	 */
	public set soundEnabled(enabled: boolean) {
		this._soundEnabled = enabled;
	}

	/**
	 * Sets the game cursor
	 * @private
	 */
	public _setCursor(): void {
		const behavior =
			this.vi.game.gameConfig.behavioursParameters[this.vi.game.gameConfig.selectedBehaviour];

		if (behavior.viParameters.icon) {
			const iconPath = `url('${this.vi.game.getThemePath()}/${
				behavior.viParameters.icon
			}') 64 0, auto`;
			const renderer = this.vi.game.renderer as any;
			// renderer.plugins.interaction.cursorStyles.default = iconPath;
			// renderer.plugins.interaction.cursorStyles.pointer = iconPath;
			// renderer.plugins.interaction.cursorStyles.hover = iconPath;
			renderer.plugins.interaction.cursorStyles.scratch = iconPath;
		} else if (behavior.viParameters.cursor) {
			this._cursor = Sprite.fromFrame(behavior.viParameters.cursor);
			this._cursor.hitArea = new Rectangle(0, 0, 0, 0);
			this.vi.game.stage.addChildAt(this._cursor, this.vi.game.stage.children.indexOf(this) + 1);

			this.vi.game.stage.on("pointermove", (e: any) => {
				const position = e.data.global;
				this._cursor.x = position.x;
				this._cursor.y = position.y;
			});

			this.vi.game.canvas.addEventListener("mouseleave", () => {
				this._cursor.visible = false;
			});

			this.vi.game.canvas.addEventListener("mouseenter", () => {
				this._cursor.visible = true;
			});

			this.vi.game.stage.interactive = true;

			const renderer = this.vi.game.renderer as any;
			renderer.plugins.interaction.cursorStyles.default = "none";
			renderer.plugins.interaction.cursorStyles.pointer = "none";
			renderer.plugins.interaction.cursorStyles.hover = "none";
			renderer.plugins.interaction.cursorStyles.scratch = "none";
		}
	}

	/**
	 * Animation to play just before the end of the splash.
	 * Needs to show the game overlay during the animation.
	 * @param customBadge You can specify your own badge here. Its docking will be set to centerAll.
	 * If customBadge is not specified it will take the prefab named SVS_VI_defaultDemoBadge.
	 */
	public async showDemoBadge(customBadge?: Container): Promise<void> {
		const demoBadge: Container = customBadge
			? customBadge
			: AssetStore.instanciatePrefab("SVS_VI_defaultDemoBadge");

		// Sets the pivot at the center off the initial scale to allow rotation from middle of the container
		const initScale: number = demoBadge.scaleXY;
		demoBadge.docking = new Docking(Docking.centerAll);
		demoBadge.pivot.set(
			demoBadge.width / demoBadge.scaleX / 2,
			demoBadge.height / demoBadge.scaleX / 2
		);

		this.addChild(demoBadge);

		// Animation
		await TweenUtils.fromTo(
			demoBadge,
			0.75,
			{ scaleXY: 0, rotation: 0, alpha: 1 },
			{ scaleXY: initScale, rotation: Math.PI * 2 }
		);
		await TweenUtils.wait(1.5);
		await TweenUtils.to(demoBadge, 0.75, { scaleXY: 0, alpha: 0 });
		demoBadge.destroy();
	}
}
