import { Graphics } from "pixi.js";
import { LoadedView } from "@gdk/core-pixi";
import type { mesh } from "pixi.js";

import { PacmanContainer } from "../components/pacmanLoading";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";
import type { IAdaptativeLoadingView } from "../../../identity";

export class LoadingView extends LoadedView implements IAdaptativeLoadingView {
	/**
	 * Defines the reference to the loading handle sprite.
	 */
	public loaderHandle: mesh.NineSlicePlane = null;

	/**
	 * container with sprites for Pacman loading screen
	 */
	protected pacmanContainer: PacmanContainer = new PacmanContainer();

	private _loadingMask: Graphics = null;

	/**
	 * Constructor
	 * @param vi the adaptative visual identity
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		// Create loading mask
		this._loadingMask = new Graphics();

		this.loaderHandle.mask = this._loadingMask;
		this.loaderHandle.addChild(this._loadingMask);

		/**
		 * Launch loading animation for VI theme: pacman
		 */
		if (
			this.vi.game.gameConfig.behavioursParameters[this.vi.game.gameConfig.selectedBehaviour]
				.viParameters.theme === "pacman"
		) {
			this.pacmanContainer.loadingAnimation();
		}
	}

	/**
	 * Defines the function called each time a file is loaded
	 * @param percentage the percentage of files already loaded
	 */
	public onProgress(percentage: number): void {
		this._loadingMask.clear();
		this._loadingMask.beginFill(0xffffff);
		this._loadingMask.drawRect(
			0,
			0,
			this.loaderHandle.width * (percentage / 100),
			this.loaderHandle.height
		);
		this._loadingMask.endFill();
	}

	/**
	 * On the view will disappear
	 */
	public onWillDisappear(): void {
		this.visible = false;
	}
}
