import { Popup, isLeftPointerTap } from "@gdk/core-pixi";
import i18n from "i18next";
import { Rectangle } from "pixi.js";
import type { interaction } from "pixi.js";
import type { Button } from "@gdk/core-pixi";

import { EVisualIdentityEvent } from "../../../events";
import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";

/**
 * We have to keep this component even if it is empty due to compatibility reasons
 */
export class SettingsComponent extends Popup {
	/**
	 * Winning table button
	 */
	public winningTableButton: Button = null;

	/**
	 * Rules button
	 */
	public rulesButton: Button = null;

	/**
	 * Constructor
	 * @param vi: the adaptative visual identity reference
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
	}

	/**
	 * Called by the project loader once the container has
	 * been imported
	 */
	public onLoaded(): void {
		this.visible = false;

		this.closeButton.setName("settings:close");
		this.closeButton.on("pointertap", (e: interaction.InteractionEvent) => {
			if (isLeftPointerTap(e)) {
				this.vi.emitter.emit(EVisualIdentityEvent.HideSettings);
				this.visible = false;
			}
		});

		this.winningTableButton.setName("settings:winningTable");
		this.winningTableButton.text.text = i18n.t("vi.button.winningTable");
		this.winningTableButton.on("pointertap", (e: interaction.InteractionEvent) => {
			if (isLeftPointerTap(e)) {
				this.vi.emitter.emit(EVisualIdentityEvent.HideSettings);
				this.vi.emitter.emit(EVisualIdentityEvent.ShowWinningTable);
				this.visible = false;
			}
		});

		this.rulesButton.setName("settings:rules");
		this.rulesButton.text.text = i18n.t("vi.button.rulesBtn");
		this.rulesButton.on("pointertap", (e: interaction.InteractionEvent) => {
			if (isLeftPointerTap(e)) {
				this.vi.emitter.emit(EVisualIdentityEvent.ShowRules);
				this.vi.emitter.emit(EVisualIdentityEvent.HideSettings);
				this.visible = false;
			}
		});

		for (const button of [this.winningTableButton, this.rulesButton]) {
			button.hitArea = new Rectangle(0, 0, button.dockingBounds.width, button.dockingBounds.height);
		}
	}

	/**
	 * Shows the settings component
	 */
	public show(): void {
		this.visible = true;
	}
}
