import { TweenMax } from "gsap";
import { LoadedView, pDelayedCall } from "@gdk/core-pixi";
import type { TextField } from "@gdk/core-pixi";
import type { Sprite } from "pixi.js";

import type { AdaptativeVisualIdentity } from "../../../adaptativeVisualIdentity";
import type { IAdaptativeIntroductionView } from "../../../identity";

export class IntroductionView extends LoadedView implements IAdaptativeIntroductionView {
	/**
	 * Logo
	 */
	protected logo: Sprite = null;

	/**
	 * Text lottery name
	 */
	protected text: TextField = null;

	/**
	 * Constructor
	 */
	public constructor(protected vi: AdaptativeVisualIdentity) {
		super();
	}

	/**
	 * On load view
	 */
	public onLoad(): void {
		this.logo.visible = false;
		this.text.visible = false;
	}

	/**
	 * Animate the view
	 * @param nextState launch the next state
	 */
	public async animate(nextState: () => void): Promise<void> {
		if (!this.vi.parameters.skipIntroduction) {
			this.logo.docking.y = this.height;
			this.logo.visible = true;

			this.text.docking.y = this.height + 250;
			this.text.visible = true;

			TweenMax.to(this.logo.docking, 1, {
				y: 0,
			});

			TweenMax.to(this.text.docking, 1, {
				y: 250,
			});

			await pDelayedCall(3);
		}

		nextState();
	}

	/**
	 * On the view will appear
	 */
	public onWillAppear(): void {
		this.visible = !this.vi.parameters.skipIntroduction;
	}
}
