import { Container } from "pixi.js";
import type { extras } from "pixi.js";

/**
 * Class for Pacman loading screen
 */
export class PacmanContainer extends Container {
	protected pacman: extras.AnimatedSprite = null;
	protected blinky: extras.AnimatedSprite = null;
	protected inky: extras.AnimatedSprite = null;
	protected clyde: extras.AnimatedSprite = null;
	protected pinky: extras.AnimatedSprite = null;

	public constructor() {
		super();
	}

	/**
	 * Play animation on each Pacman sprites
	 */
	public loadingAnimation(): void {
		if (
			this.pacman !== null &&
			this.blinky !== null &&
			this.inky !== null &&
			this.clyde !== null &&
			this.pinky !== null
		) {
			this.pacman.gotoAndPlay(0);
			this.blinky.gotoAndPlay(0);
			this.inky.gotoAndPlay(0);
			this.clyde.gotoAndPlay(0);
			this.pinky.gotoAndPlay(0);
		}
	}
}
